<template>
  <div>
    <div v-if="!mobile" class="container">
      <div class="headPortrait">
        <div class="head">
          <img :src="userInfo.iconUrl" alt="头像" />
        </div>
        <div class="userName">
          <span>{{userInfo.nickName}}</span>
        </div>
        <div class="modify mainBtn" @click="CropperBlClcik">
          <!-- <div class="inputFile1"
            @mouseenter="inputFileBl=true"
            @mouseleave="inputFileBl=false">
          </div>-->
          <span :class="{headPortraitSpan1:inputFileBl,headPortraitSpan:true}">修改头像</span>
          <input
            type="file"
            :value="fileValue"
            class="inputFile1"
            @mouseenter="inputFileBl=true"
            @mouseleave="inputFileBl=false"
            @change="upload"
          />
        </div>
      </div>
      <div class="rightsidebar">
        <ul>
          <li
            :class="{active:$route.name=='ctrlMenu'}"
            @mouseenter="liIndex=5"
            @mouseleave="liIndex=99999"
            v-if="userInfo.identity==2"
          >
            <router-link :to="{name:'ctrlMenu'}">
              <i class="icon userCenterFont icon-console" :style="liIndex==5?'color:#3dacfe':''"></i>
              <span class="rightsidebarTxt" :style="liIndex==5?'color:#3dacfe':''">控制面板</span>
            </router-link>
          </li>
          <li
            :class="{active:$route.name=='runVIP'}"
            @mouseenter="liIndex=6"
            @mouseleave="liIndex=99999"
            v-if="userInfo.identity==2"
          >
            <router-link :to="{name:'runVIP'}">
              <i
                class="icon userCenterFont icon-memberManage"
                :style="liIndex==6?'color:#3dacfe':''"
              ></i>
              <span class="rightsidebarTxt" :style="liIndex==6?'color:#3dacfe':''">会员管理</span>
            </router-link>
          </li>
          <li
            :class="{active:$route.name=='runOrder'}"
            @mouseenter="liIndex=7"
            @mouseleave="liIndex=99999"
            v-if="userInfo.identity==2"
          >
            <router-link :to="{name:'runOrder'}">
              <i
                class="icon userCenterFont icon-orderManage"
                :style="liIndex==7?'color:#3dacfe':''"
              ></i>
              <span class="rightsidebarTxt" :style="liIndex==7?'color:#3dacfe':''">订单管理</span>
            </router-link>
          </li>
          <router-link :to="{name:'accountInfo'}">
            <li
              :class="{active:$route.name=='accountInfo'||$route.name=='consumingRecord'||$route.name=='incomeAmount'||$route.name=='rechargeRecord'}"
              @mouseenter="liIndex=3"
              @mouseleave="liIndex=99999"
            >
              <i
                class="icon userCenterFont icon-accountBalance"
                :style="liIndex==3?'color:#3dacfe':''"
              ></i>
              <span class="rightsidebarTxt" :style="liIndex==3?'color:#3dacfe':''">账户总览</span>
              <router-link class="topUp tipsBtn" :to="{name:'accountInfo'}">充值</router-link>
            </li>
          </router-link>
          <router-link :to="{name:'renew'}">
            <li
              :class="{active:$route.name=='renew'}"
              @mouseenter="liIndex=4"
              @mouseleave="liIndex=99999"
            >
              <i class="icon userCenterFont icon-renew" :style="liIndex==4?'color:#3dacfe':''"></i>
              <span class="rightsidebarTxt" :style="liIndex==4?'color:#3dacfe':''">开通续费</span>
            </li>
          </router-link>
          <router-link :to="{name:'basicInfo'}">
            <li
              :class="{active:$route.name=='basicInfo'||$route.name=='userCenter'}"
              @mouseenter="liIndex=0"
              @mouseleave="liIndex=99999"
            >
              <i class="icon userCenterFont icon-basicInfo" :style="liIndex==0?'color:#3dacfe':''"></i>
              <span class="rightsidebarTxt" :style="liIndex==0?'color:#3dacfe':''">基本资料</span>
            </li>
          </router-link>
          <router-link :to="{name:'certification'}">
            <li
              :class="{active:$route.name=='certification'}"
              @mouseenter="liIndex=2"
              @mouseleave="liIndex=99999"
            >
              <i
                class="icon userCenterFont icon-certification"
                :style="liIndex==2?'color:#3dacfe':''"
              ></i>
              <span class="rightsidebarTxt" :style="liIndex==2?'color:#3dacfe':''">实名认证</span>
            </li>
          </router-link>
          <router-link :to="{name:'securitySetting'}">
            <li
              :class="{active:$route.name=='securitySetting'}"
              @mouseenter="liIndex=1"
              @mouseleave="liIndex=99999"
            >
              <i
                class="icon userCenterFont icon-securitySetting"
                :style="liIndex==1?'color:#3dacfe':''"
              ></i>
              <span class="rightsidebarTxt" :style="liIndex==1?'color:#3dacfe':''">安全设置</span>
            </li>
          </router-link>
        </ul>
      </div>
    </div>
    <router-view :class="{view: !mobile}"></router-view>
    <Cropper :cropperBl="cropperBl" v-if="croBl" :cropperImg="cropperImg"></Cropper>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Cropper from "@/pages/UserCenter/UserItem/Cropper.vue";
export default {
  components: {
    Cropper
  },
  name: "UserCenterSidebar",
  data() {
    return {
      // 默认头像
      userBackgroundIimage: "",
      fileValue: "",
      liIndex: 999,
      inputFileBl: false,
      croBl: false,
      mobile: false,
      cropperImg: "",
      cropperBl: {
        callback() {},
        tipFn() {}
      }
    };
  },
  methods: {
    // 修改用户头像
    CropperBlClcik() {
      var vm = this;
      vm.cropperBl.callback = function() {
        vm.croBl = false;
      };
      vm.cropperBl.tipFn = function() {
        vm.$store.dispatch("publicTools/setTipsData", ["修改成功", true]);
      };
    },
    upload(e) {
      let formData = new FormData();
      formData.append("iconUrl", e.target.files[0]);
      this.$post("api/iconUrl", formData).then(res => {
        localStorage.setItem("headimgurl", res.data);
        const data = {
          nickName: this.userInfo.nickName,
          iconUrl: res.data
        };
        this.$store.commit("userCenter/setUserInfo", data);
      });
    }
  },
  computed: {
    ...mapGetters("userCenter", {
      userInfo: "getUserInfo"
    })
  },
  mounted() {
    /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)
      ? (this.mobile = true)
      : (this.mobile = false);
  }
};
</script>

<style scoped>
.rightsidebarTxt {
  -moz-user-select: none;
  -o-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.container {
  width: 14.1vw;
  height: 100%;
  background-color: #18202e;
  position: absolute;
}

.container .headPortrait {
  width: 100%;
  padding: 5.1vh 2.6vw 3.2vh 2.1vw;
  border-bottom: #0c1017 solid 0.05vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container .headPortrait .head {
  width: 4.7vw;
  height: 4.7vw;
  border-radius: 50%;
  background-size: 125% auto;
  background-repeat: no-repeat;
  background-position: center;
}
.container .headPortrait .head img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.container .headPortrait .userName {
  font-size: 0.9vw;
  line-height: 1.9vh;
  height: 1.9vh;
  width: 8vw;
  color: #fffefe;
  margin: auto;
  margin-top: 1.9vh;
  text-align: center;
  position: relative;
}
.container .headPortrait .userName span {
  text-overflow: ellipsis;
  display: inline-block;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.container .headPortrait .userName .edit {
  font-size: 0.6vw;
  margin-left: 0.5vw;
  cursor: pointer;
  color: #28b3fc;
  position: absolute;
  right: -2vw;
  top: 0;
}
.inputFile {
  position: absolute;
  width: 10vw;
  height: 20vh;
  top: -5.2vh;
  left: -5.2vw;
  margin: 0;
  padding: 0;
  opacity: 0;
  z-index: 9;
  cursor: pointer;
}
.inputFile1 {
  position: absolute;
  width: 4.5vw;
  height: 2vh;
  top: 0.2vh;
  left: 0.2vw;
  margin: 0;
  padding: 0;
  opacity: 0;
  z-index: 9;
  cursor: pointer;
  font-size: 0;
  overflow: hidden;
}
.container .headPortrait .modify {
  position: relative;
  text-align: center;
  height: 2.2vh;
  margin: auto;
  width: 5.4vw;
  font-size: 0.6vw;
  margin-top: 1.4vh;
  line-height: 2.2vh;
  cursor: pointer;
  border: 0;
  background-color: rgb(24, 32, 46);
}
.container .headPortrait .modify:hover {
  border: 0;
  background-color: rgb(24, 32, 46);
}

.container .rightsidebar {
  border-top: #2d384f solid 0.05vw;
  padding-top: 1.1vh;
}

.container .rightsidebar ul li {
  height: 5.3vh;
  line-height: 5.3vh;
  padding-left: 1.9vw;
  font-size: 0.8vw;
  box-sizing: content-box;
}

.container .rightsidebar ul li.active {
  border-left: solid 0.2vw #3dadff;
  background-color: #11151f;
  padding-left: 1.7vw;
}
.container .rightsidebar ul li.active a {
  color: #3dacfe;
}

.container .rightsidebar ul li i {
  font-size: 0.8vw;
  vertical-align: 0;
  margin-right: 0.7vw;
}

.container .rightsidebar ul li .topUp {
  padding: 0 0.3vw;
  font-size: 0.6vw;
  position: absolute;
  right: 0.6vw;
  margin-top: 1.7vh;
  line-height: 2vh;
  color: #fff;
}
.view {
  position: absolute;
  left: 15.1vw;
  top: 4.2vh;
}
.modify .headPortraitSpan {
  font-size: 0.6vw;
  line-height: 1.3vh;
  padding: 0.4vh 0.6vw;
  cursor: pointer;
  background-color: #283247;
  border: solid 0.0625vw #283247;
}
.modify .headPortraitSpan1 {
  border: solid 0.0625vw #28b3fc;
  background-color: #1d4c6c;
}
.modify .headPortraitSpan:hover {
  border: solid 0.0625vw #28b3fc;
  background-color: #1d4c6c;
}
</style>
