<template>
  <div>
    <div class="maskBackground">
      <div class="box1 allCenter">
        <div class="title">{{EmailData.title}}</div>
        <div class="editAvatar">
          <div class="bigAvatar">
            <VueCropper ref="cropper"
              :img="option.img"
              :outputSize="option.size"
              :outputType="option.outputType"
              :info="true"
              :full="option.full"
              :canMove="option.canMove"
              :canMoveBox="option.canMoveBox"
              :fixedBox="option.fixedBox"
              :original="option.original"
              :autoCrop="option.autoCrop"
              :autoCropWidth="option.autoCropWidth"
              :autoCropHeight="option.autoCropHeight"
              :centerBox="option.centerBox"
              :high="option.high"
              :infoTrue="option.infoTrue"
              :maxImgSize="option.maxImageSize"
              @realTime="realTime"
              @imgLoad="imgLoad"
              @cropMoving="cropMoving"
              :enlarge="option.enlarge"
              :mode="option.mode"></VueCropper>

          </div>
          <div class="minAvatar">
            <div style="width:6.3vw;height:6.3vw;">

              <div class="show-preview"
                :style="{'width': previewStyle1.w, 'height': previewStyle1.h ,  'overflow': 'hidden',
							}">
                <div :style="previews.div">
                  <img :src="previews.url"
                    :style="previews.img">
                </div>
              </div>
            </div>
            <span>头像预览</span>
          </div>
        </div>
        <div class="ctrlAvatarDiv">
          <span class="ctrlAvatarDivFl">重新上传</span>
          <input type="file"
            class="ctrlAvatarDivFl"
            @change="upload($event, 1)">
          <img src="@/assets/UserCenter/imgs/xuanzhuan.svg"
            alt=""
            class="ctrlAvatarDivFr"
            v-if="xuanzhuanImgBl"
            @click="rotateLeft"
            @mouseover="xuanzhuanImgBl=false">
          <img src="@/assets/UserCenter/imgs/xuanzhuan-blue.svg"
            alt=""
            class="ctrlAvatarDivFr"
            @mouseleave="xuanzhuanImgBl=true"
            @click="rotateLeft"
            v-if="!xuanzhuanImgBl">
        </div>
        <div class="moduleFoot">
          <div class="moduleFootDiv">
            <span @click="down('blob')">确认</span>
            <!-- <span @click="cropperBl.postClick('blob')">确认</span> -->
            <span @click="cropperBl.callback">取消</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { VueCropper } from 'vue-cropper';
export default {
	components: {
		VueCropper
	},
	props: ['cropperBl', 'cropperImg'],
	data() {
		return {
			EmailData: {
				title: '上传头像'
			},
			xuanzhuanImgBl: true,
			option: {
				img: this.cropperImg,
				size: 1,
				full: false,
				outputType: 'jpg',
				canMove: true,
				fixedBox: true,
				original: false,
				canMoveBox: true,
				autoCrop: true,
				// 只有自动截图开启 宽度高度才生效
				autoCropWidth: '120',
				autoCropHeight: '120',
				centerBox: true,
				high: false,
				cropData: {},
				enlarge: 1,
				mode: 'contain',
				maxImgSize: 2000,
				fixed: false,
				fixedNumber: [1, 1]
			},
			previewStyle1: {},
			previews: {}
		};
	},
	methods: {
		// 实时预览函数
		realTime(data) {
			var previews = data;
			var h = 0.5;
			var w = 0.2;
			this.previewStyle1 = {
				width: '6.3vw',
				height: '6.3vw',
				overflow: 'hidden',
				margin: '0',
				zoom: h
			};
			this.previews = previews;
		},

		imgLoad(msg) {},
		cropMoving(data) {
			this.option.cropData = data;
		},
		rotateLeft() {
			this.$refs.cropper.rotateLeft();
		},
		upload(e, num) {
			//上传图片
			// this.option.img
			var file = e.target.files[0];
			if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {
		  this.tips.show(['上传文件失败']);
				// this.tipsData.rel = 'fail';
				// this.tipsData.text = '上传文件失败';
				// this.$refs.tips.closeTips();
				return false;
			}
			var reader = new FileReader();
			reader.onload = e => {
				let data;
				if (typeof e.target.result === 'object') {
					// 把Array Buffer转化为blob 如果是base64不需要
					data = window.URL.createObjectURL(new Blob([e.target.result]));
				} else {
					data = e.target.result;
				}
				if (num === 1) {
					this.option.img = data;
				} else if (num === 2) {
					this.example2.img = data;
				}
			};
			// 转化为base64
			// reader.readAsDataURL(file)
			// 转化为blob
			var test = reader.readAsArrayBuffer(file);
		},
		down(type) {
			if (type === 'blob') {
				this.$refs.cropper.getCropBlob(data => {
					this.downImg = data;
					var formData = new FormData();
					formData.append('image', this.downImg);
					this.$post('api/personageMessage', formData).then(res => {
						if (res.code == '000000') {
							this.$store.commit('userCenter/setUserInfo', res.data);
							this.cropperBl.callback();
							this.cropperBl.tipFn();
						}
					});
				});
			} else {
				this.$refs.cropper.getCropData(data => {
					this.downImg = data;
					this.$post('api/personageMessage', {
						image: this.downImg
					}).then(res => {});
				});
			}
		}
	},
	mounted() {}
};
</script>
<style scoped>
.box1 {
  width: 26.4vw;
  height: 27.8vw;
  background-color: #19202e;
  text-align: center;
}
.title {
  font-family: SourceHanSansCN-Regular;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0vw;
  color: #ffffff;
  line-height: 1.9vw;
  font-size: 0.8vw;
  width: 26.4vw;
  height: 1.9vw;
  background-color: #273045;
  margin-bottom: 0.9vw;
}
.editAvatar {
  margin-left: 1.9vw;
  margin-top: 2vw;
  width: 24.5vw;
  height: 15.6vw;
}
.bigAvatar {
  float: left;
  width: 15.7vw;
  height: 15.6vw;
  overflow: hidden;
}
.minAvatar {
  float: left;
  width: 6.3vw;
  height: 7.3vw;
  margin-left: 0.6vw;
  text-align: center;
}
.minAvatar div {
  width: 6.3vw;
  height: 6.3vw;
  margin-bottom: 0.3vw;
  border-radius: 50%;
}
.minAvatar span {
  font-family: SourceHanSansCN-Regular;
  font-size: 0.7vw;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0vw;
  color: #ffffff;
}
.ctrlAvatarDiv {
  margin-left: 1.9vw;
  width: 15.7vw;
  height: 1.4vw;
  padding-top: 0.5vw;
  position: relative;
}
.ctrlAvatarDiv input {
  position: absolute;
  left: 0;
  top: 0.4vw;
  width: 3vw;
  height: 1vw;
  cursor: pointer;
  opacity: 0;
  cursor: pointer;
}
.ctrlAvatarDivFl {
  float: left;
  font-size: 0.7vw;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0vw;
  color: #28b3fc;
  cursor: pointer;
}
.ctrlAvatarDivFr {
  float: right;
  width: 0.7vw;
  height: 0.8vw;
  cursor: pointer;
}
.moduleFoot {
  width: 26.4vw;
  height: 4.1vw;
  margin-top: 1.4vw;
  border-top: #080b0f solid 0.0625vw;
  position: relative;
}
.moduleFootDiv {
  position: absolute;
  bottom: 0;
  right: 0.7vw;
}
.moduleFootDiv span {
  margin-right: 1.4vw;
  display: inline-block;
  width: 3.7vw;
  height: 1.4vw;
  font-family: SourceHanSansCN-Regular;
  font-size: 0.7vw;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0vw;
  color: #ffffff;
  line-height: 1.3vw;
  background-color: #273045;
  border: solid 0.0625vw #273045;
  cursor: pointer;
}
.moduleFootDiv span:hover {
  background-color: #1c3d57;
  border: solid 0.0625vw #28b3fc;
}
.cropper-view-box {
  border-radius: 50%;
}
.maskBackground {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
  color: #fff;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.box1 {
  z-index: 999;
  width: 26.4vw;
  height: 27.8vw;
  background-color: #19202e;
  text-align: center;
}
</style>


